  showProperty(connector, index) {
    this.isSidenavOpen = true;
    this.selectedConnectorId = connector.connector_no;
    this.currentActiveId = '';
    this.getActiveTransactionId(connector);
    this.getCancelTransactionId(connector);
    if (this.connectorAction == "Manage Configurations") {
      this.getConfiguration(connector);
    }

    var down = 'down' + index;
    var up = 'up' + index;
    for (var d = 0; d < this.chargerDetailsObj.connector_data.length; d++) {
      var firstDiv = document.getElementById('' + d);
      down = 'down' + d;
      up = 'up' + d;
      var downBtn = document.getElementById(down);
      var upBtn = document.getElementById(up);
      if (index == d) {
        firstDiv.style.display = 'block';
        downBtn.style.display = 'none';
        upBtn.style.display = 'block';
      } else {
        firstDiv.style.display = 'none';
        downBtn.style.display = 'block';
        upBtn.style.display = 'none';
      }
    }
    this.page = 1;
    this.occpPage = 1;
    //this.getAllConfigurationData();
  }
  hideProperty(connector, index) {
    var d = index;
    var firstDiv = document.getElementById(d);
    firstDiv.style.display = 'none';
    var down = 'down' + d;
    var up = 'up' + d;
    var downBtn = document.getElementById(down);
    downBtn.style.display = 'block';
    var upBtn = document.getElementById(up);
    upBtn.style.display = 'none';
  }

  changeAvailability(connector) {
    if (connector.availabilityType != null) {
      let params = {
        "command": "CHANGE_AVAILABILITY",
        "charger_id": this.chargerDetailsObj.name,
        "charger_sr_no": this.chargerDetailsObj.serial_no,
        "connector": connector.connector_no,
        "type": connector.availabilityType
      }

      this.chargingStationService.changeAvailability(params).subscribe((data: any) => {
        //
        if (data.status) {
          this.logs = data;
          this.snackBar.open(data.message, 'Dismiss', { duration: 3000 });
        }
        this.snackBar.open(ChLogsMessageConstant.errorMessage, 'Dismiss', { duration: 3000 });
        //;

      }, (err) => {
        this.snackBar.open(ChLogsMessageConstant.chargerAlreadyStarted, 'Dismiss', { duration: 3000 });

      })
    } else {
      this.snackBar.open(ChLogsMessageConstant.errorAvailabilty, 'Dismiss', { duration: 3000 });
      return;
    }

  }

  remoteStart(connector) {
    var formValues = this.remoteStartform.value;
    if (this.stationDetails != null && this.stationDetails != undefined) {
      let tagType;
      if (connector.selectedTagType == 'OTP') {
        tagType = 'OTP'
      } else {
        tagType = 'RF_ID'
      }

      if (connector.ocppIdTag != null) {
        let params = {
          command: 'START_CHARGING',
          charger_id: this.chargerDetailsObj.name,
          charger_sr_no: this.chargerDetailsObj.serial_no,
          connector: formValues.is_dual_gun == false ? +connector.connector_no : 0,
          id_tag: connector.ocppIdTag.rf_id_no,//"8855CB4AA168E04"
          id_tag_type: tagType,
          user_id: this.localStorage.userId,
          command_source: "web",
          device_id: null,
          app_version: null,
          os_version: null,
          station_id: this.stationDetails,
          mobile_no: null,
          vehicle_id: null,
          vehicle_number: null,
          booking_id: " "
        }
        this.chargingStationService.startChargingStation(params).subscribe((data: any) => {

          if (data.status) {
            this.logs = data;
            connector.ocppIDTag = "";
            this.snackBar.open(ChLogsMessageConstant.chargerStarted, 'Dismiss', { duration: 3000 });
          } else {
            // this.snackBar.open(ChLogsMessageConstant.errorMessage, 'Dismiss', { duration: 3000 });
            this.snackBar.open(data.message, 'Dismiss', { duration: 3000 });
            connector.ocppIDTag = "";
          }



        }, (err) => {
          this.snackBar.open(ChLogsMessageConstant.errorMessage, 'Dismiss', { duration: 3000 });

        })
      }

      else {
        if (connector.otp != null) {
          let params = {
            command: 'START_CHARGING',
            charger_id: this.chargerDetailsObj.name,
            charger_sr_no: this.chargerDetailsObj.serial_no,
            connector: formValues.is_dual_gun == false ? +connector.connector_no : 0,
            id_tag: connector.otp,//"8855CB4AA168E04"
            id_tag_type: tagType,
            user_id: this.localStorage.userId,
            command_source: "web",
            device_id: null,
            app_version: null,
            os_version: null,
            station_id: this.stationDetails,
            mobile_no: null,
            vehicle_id: null,
            vehicle_number: null
          }
          this.chargingStationService.startChargingStation(params).subscribe((data: any) => {
            if (data.status) {
              this.logs = data;
              connector.ocppIDTag = "";
              this.snackBar.open(ChLogsMessageConstant.chargerStarted, 'Dismiss', { duration: 3000 });
            } else {
              this.snackBar.open(data.message, 'Dismiss', { duration: 3000 });
              connector.ocppIDTag = "";
            }



          }, (err) => {
            this.snackBar.open(ChLogsMessageConstant.errorMessage, 'Dismiss', { duration: 3000 });

          })
        }
        else {
          this.snackBar.open(ChLogsMessageConstant.errorOCPPIdTag, 'Dismiss', { duration: 3000 });
          return;
        }
      }
    } else {
      this.snackBar.open(ChLogsMessageConstant.stationNotFound, 'Dismiss', { duration: 3000 });
    }



  }
