  getChargingRateUnit() {
    this.chargingProfileService.getChargingRateUnit().subscribe(
      (resp: any) => {
        if (resp.status == true) {
          this.charRateUnit = resp.data;
          this.filteredOptionsCharRateUnit = this.getCompositeScheduleGroup.controls.chargingRateUnit.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.value),
              map(name => name ? this._filterChargingRateUnit(name) : this.charRateUnit)
            );
        }
      }, (err) => {

      }
    );
  }
  private _filterChargingRateUnit(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.charRateUnit.filter(option => option.value.toLowerCase().includes(filterValue));
  }
  displayCharRateUnit(item: any): string {
    return item && item.value ? item.value : '';
  }

