

import { Component, OnInit,Inject,ViewChild } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {ViewEncapsulation} from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { MatSnackBar } from '@angular/material/snack-bar';
import {MessageConstant} from 'app/shared/utils/message.constant';
import { Clipboard } from '@angular/cdk/clipboard';
interface DialogData {
	data: string;
	action:string;
  }
@Component({
  selector: 'app-acl-json-modal',
  templateUrl: './ack-json-modal.component.html',
  styleUrls: ['./ack-json-modal.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations
})
export class AckModalComponent implements OnInit {
 
  
   localStorage : any;
   jsonData: any;
   expanded= true;
   action:string;
  constructor(public dialogRef: MatDialogRef<AckModalComponent>, @Inject(MAT_DIALOG_DATA) public data: DialogData,
   private snackBar : MatSnackBar,private clipboard: Clipboard) {
     
    
  }

  ngOnInit() {
    this.initialize();
   
  }

  initialize(){
    

    this.localStorage = JSON.parse( localStorage.getItem('userDetails'));
     this.jsonData = JSON.parse(this.data.data);
	 this.action = this.data.action;
    
  }

  copyJson() {
    this.clipboard.copy(JSON.stringify(this.jsonData));
	if(this.action == "REQUEST"){
		this.snackBar.open(MessageConstant.requestCopied, 'Dismiss',{duration : 5000 });
	}else{
		this.snackBar.open(MessageConstant.responseCopied, 'Dismiss',{duration : 5000 });
	}
    
  }
 
}



